const IndexPage = () => {
  const particlesInit = useCallback(async (engine) => {
    await loadPolygonPath(engine);
  }, []);

  return (
    <Layout bodyClass={null}>
      <Particles
        options={{
          fpsLimit: 60,
          particles: {
            color: {
              value: ["#6D28D9", "#F59E0B", "#9333EA"],
              animation: {
                enable: true,
                speed: 1,
              },
            },
            move: {
              attract: {
                enable: true,
                rotate: {
                  distance: 100,
                  x: 2000,
                  y: 2000,
                },
              },
              direction: "none",
              enable: true,
              outModes: {
                default: "destroy",
              },
              path: {
                clamp: false,
                enable: true,
                delay: {
                  value: 0,
                },
                generator: polygonPathName,
                options: {
                  sides: 6,
                  turnSteps: 30,
                  angle: 30,
                },
              },
              random: false,
              speed: 3.5,
              straight: false,
              trail: {
                fillColor: "#000",
                length: 20,
                enable: true,
              },
            },
            number: {
              density: {
                enable: true,
                area: 800,
              },
              value: 0,
            },
            opacity: {
              value: 1,
            },
            shape: {
              type: "circle",
            },
            size: {
              value: 2,
            },
          },
          background: {
            color: "#000",
          },
          fullScreen: {
            zIndex: -1,
          },
          detectRetina: true,
          emitters: {
            direction: "none",
            rate: {
              quantity: 2,
              delay: 0.3,
            },
            size: {
              width: 0,
              height: 0,
            },
            position: { x: 65, y: 40 },
          },
        }}
        init={particlesInit}
      />

      <div className="grid grid-cols-1 items-center divide-y-0">
        <div className="mt-15 font-hairline relative flex self-start bg-transparent p-10 font-sans shadow-lg shadow-indigo-400/50">
          <div className="container ml-10 h-1/2 w-1/2 justify-center p-20">
            <h1 className="text-3xl leading-tight text-white md:text-5xl">
              Take your IT to the Next Level.
            </h1>
            <p className="pt-4 text-lg text-slate-400">
              Serving the greater Houston Area and beyond. Lorem ipsum dolor sit
              amet, consectetur adipiscing elit, sed do eiusmod tempor
              incididunt ut labore et dolore magna aliqua. Ut enim ad minim
              veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip
              ex ea commodo consequat.
            </p>
            <div className="container mt-10 w-9/12 self-start pr-6">
              <div
                className="flex flex-col items-start rounded bg-white p-4 text-sm sm:flex-row sm:items-center"
                style={{
                  boxShadow:
                    "0 15px 35px rgba(50,50,93,.1), 0 5px 15px rgba(0,0,0,.07)",
                }}
              >
                <div className="flex-1">
                  <div className="flex flex-row items-center space-x-3 pb-1">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="align-center h-5 w-5 justify-center"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path
                        stroke-linecap="round"
                        stroke-linejoin="round"
                        stroke-width="2"
                        d="M21 3l-6 6m0 0V4m0 5h5M5 3a2 2 0 00-2 2v1c0 8.284 6.716 15 15 15h1a2 2 0 002-2v-3.28a1 1 0 00-.684-.948l-4.493-1.498a1 1 0 00-1.21.502l-1.13 2.257a11.042 11.042 0 01-5.516-5.517l2.257-1.128a1 1 0 00.502-1.21L9.228 3.683A1 1 0 008.279 3H5z"
                      />
                    </svg>
                    <p>
                      <strong>Phone: </strong>
                      XXX XXX XXX
                    </p>
                  </div>
                  <div className="flex flex-row items-center space-x-3 pb-1">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-5 w-5"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path
                        stroke-linecap="round"
                        stroke-linejoin="round"
                        stroke-width="2"
                        d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z"
                      />
                    </svg>
                    <p>
                      <strong>Email: </strong>
                      support@maxlevel.tech
                    </p>
                  </div>
                </div>
                <p className="pr-9 text-lg text-slate-400">-OR-</p>
                <div className="mt-6 flex-initial sm:mt-0">
                  <Link to="/contact">
                    <button className="hover:-translate-1 rounded bg-indigo-700 py-2 px-4 text-base font-bold uppercase text-white shadow-md hover:bg-indigo-600">
                      Contact
                    </button>
                  </Link>
                </div>
              </div>
            </div>
          </div>

          <div className="mx-auto mt-5 flex w-4/12 justify-center">
            <CityscapeImage className="invisible mx-auto" />
          </div>
        </div>
        <div className="shadow-lg shadow-indigo-700/50">
          <Services />
        </div>
        <div>
          <Team />
        </div>
        <div className="shadow-lg shadow-indigo-700/50">
          <Pricing />
        </div>
        <div className="shadow-lg shadow-indigo-700/50">
          <Contact />
        </div>
      </div>
    </Layout>
  );
};

export default IndexPage;
